.footer-top {
  position: relative;
  z-index: 2;
  color: #333;
  background: #FFB800;
  backdrop-filter: blur(0.5rem);
  padding: 1.125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 2rem;
  }
  &__logo {
    font-family: 'Oswald';
    span:nth-child(1) {
      display: block;
      font-size: 1.25rem;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
    }
    span:nth-child(2) {
      font-size: 0.875rem;
      line-height: 120%;
    }
  }
  &__items {
    display: flex;
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 5.625rem;
    }
    &-icon {
      flex-shrink: 0;
      width: 1.25rem;
      margin-right: 1.25rem;
      svg {
        width: 100%;
      }
    }
    &-text span {
      font-size: 0.875rem;
      line-height: 150%;
    }
    &-text a {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      &:nth-child(2) {
        text-decoration: underline dotted;
        text-underline-offset: 0.1875rem;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 5.625rem;
  }
  &__phone {
    display: flex;
    align-items: center;
    font-family: 'Oswald';
    i {
      flex-shrink: 0;
      width: 1.25rem;
      display: flex;
      align-items: center;
      margin-right: 1.25rem;
      svg {
        width: 100%;
      }
    }
    span {
      font-size: 1.25rem;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    height: 2.5rem;
    background: transparent;
    border: 0.0625rem solid #333;
    border-radius: 3.125rem;
    padding: 0 1.5rem;
    margin-left: 0.75rem;
    span {
      font-size: 0.875rem;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.043749rem;
      text-transform: uppercase;
    }
  }
}
.footer-bottom {
  background: #F5F5F5;
  padding: 0.9375rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__b2b {
    flex-shrink: 0;
    display: inline-flex;
    align-items: center;
    background: #2C2C2C;
    border-radius: 1.25rem;
    padding: 0.4375rem 1.125rem;
    margin-right: 2rem;
    margin-bottom: 0.875rem;
    span {
      font-size: 0.625rem;
      line-height: 130%;
      color: #FFF;
    }
    i {
      flex-shrink: 0;
      width: 3.4375rem;
      margin-left: 0.625rem;
      img {
        width: 100%;
      }
    }
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    p {
      position: relative;
      font-size: 0.875rem;
      line-height: 130%;
      color: #333;
      margin-top: 0;
      margin-bottom: 0.875rem;
    }
    p:not(:last-child) {
      margin-right: 2rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.1875rem;
        right: -1rem;
        transform: translate(0, 0) rotate(15deg);
        width: 0.0625rem;
        height: 0.75rem;
        background: #333333;
      }
    }
    p a {
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
    }
  }
}

@media screen and (max-width: 1520px) {
  .footer-top {
    &__right {
      margin-left: 1rem;
    }
    &__logo {
      span:nth-child(1) {
        font-size: 1rem;
      }
      span:nth-child(2) {
        font-size: 0.8125rem;
      }
    }
    &__item {
      &:not(:first-child) {
        margin-left: 1rem;
      }
      &-icon {
        margin-right: 0.625rem;
      }
      &-text span {
        font-size: 0.8125rem;
      }
      &-text a {
        font-size: 0.8125rem;
      }
    }
    &__btns {
      margin-left: 1rem;
    }
    &__phone {
      i {
        margin-right: 0.625rem;
      }
      span {
        font-size: 0.875rem;
      }
    }
    &__btn {
      span {
        font-size: 0.8125rem;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    &__inner {
      align-items: start;
    }
    &__right {
      justify-content: space-between;
      align-items: start;
    }
    &__logo {
      span:nth-child(1) {
        font-size: 1.25rem;
      }
      span:nth-child(2) {
        font-size: 0.875rem;
      }
    }
    &__items {
      display: block;
    }
    &__item {
      &:not(:first-child) {
        margin: 1.25rem 0 0 0;
      }
      &-text span {
        font-size: 0.875rem;
      }
    }
    &__btns {
      display: block;
    }
    &__phone {
      justify-content: flex-end;
    }
    &__btn {
      margin: 1.25rem 0 0 0;
      span {
        font-size: 0.875rem;
      }
    }
  }
  .footer-bottom {
    &__inner {
      display: block;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-bottom {
    &__b2b {
      margin-right: 0;
    }
    &__info {
      display: block;
      p:not(:last-child) {
        margin-right: 0;
      }
      p:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .footer-top {
    &__inner {
      display: block;
    }
    &__right {
      display: block;
      margin: 2rem 0 0 0;
    }
    &__btns {
      margin: 1.25rem 0 0 0;
    }
    &__phone {
      justify-content: start;
    }
  }
}