.header {
  position: relative;
  z-index: 2;
  color: #333;
  background: #FFB800;
  backdrop-filter: blur(0.5rem);
  padding: 1.125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 2rem;
  }
  &__logo {
    font-family: 'Oswald';
    span:nth-child(1) {
      display: block;
      font-size: 1.25rem;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
    }
    span:nth-child(2) {
      font-size: 0.875rem;
      line-height: 120%;
    }
  }
  &__items {
    display: flex;
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 5.625rem;
    }
    &-icon {
      flex-shrink: 0;
      width: 1.25rem;
      margin-right: 1.25rem;
      svg {
        width: 100%;
      }
    }
    &-text span {
      font-size: 0.875rem;
      line-height: 150%;
    }
    &-text a {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      &:nth-child(2) {
        text-decoration: underline dotted;
        text-underline-offset: 0.1875rem;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 5.625rem;
  }
  &__phone {
    display: flex;
    align-items: center;
    font-family: 'Oswald';
    i {
      flex-shrink: 0;
      width: 1.25rem;
      display: flex;
      align-items: center;
      margin-right: 1.25rem;
      svg {
        width: 100%;
      }
    }
    span {
      font-size: 1.25rem;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    height: 2.5rem;
    background: transparent;
    border: 0.0625rem solid #333;
    border-radius: 3.125rem;
    padding: 0 1.5rem;
    margin-left: 0.75rem;
    span {
      font-size: 0.875rem;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.043749rem;
      text-transform: uppercase;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 1.875rem;
  height: 1.125rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.125rem;
    background: #333;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 0.5rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.5rem;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1520px) {
  .header {
    &__right {
      margin-left: 1rem;
    }
    &__logo {
      span:nth-child(1) {
        font-size: 1rem;
      }
      span:nth-child(2) {
        font-size: 0.8125rem;
      }
    }
    &__item {
      &:not(:first-child) {
        margin-left: 1rem;
      }
      &-icon {
        margin-right: 0.625rem;
      }
      &-text span {
        font-size: 0.8125rem;
      }
      &-text a {
        font-size: 0.8125rem;
      }
    }
    &__btns {
      margin-left: 1rem;
    }
    &__phone {
      i {
        margin-right: 0.625rem;
      }
      span {
        font-size: 0.875rem;
      }
    }
    &__btn {
      span {
        font-size: 0.8125rem;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100vh;
      display: block;
      background: #FFB800;
      backdrop-filter: blur(0.5rem);
      padding: 5rem 0.9375em 3.125rem;
      margin: 0;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
    }
    &__logo {
      span:nth-child(1) {
        font-size: 1.25rem;
      }
      span:nth-child(2) {
        font-size: 0.875rem;
      }
    }
    &__items {
      display: block;
    }
    &__btns {
      display: block;
      margin: 1rem 0 0 0;
    }
    &__item {
      &:not(:first-child) {
        margin: 1rem 0 0 0;
      }
      &-text span {
        font-size: 0.875rem;
      }
    }
    &__btn {
      margin: 1rem 0 0 0;
      span {
        font-size: 0.875rem;
      }
    }
  }
  .burger {
    display: block;
  }
}