.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #333;
  padding: 7.5rem 0 21.875rem;
  position: relative;
  h1 {
    span {
      font-size: 3.125rem;
    }
  }
  &__subtitle {
    max-width: 41.625rem;
    font-size: 2.125rem;
    line-height: 130%;
    margin-top: 2.5rem;
    span {
      font-weight: 700;
    }
  }
  &__btn {
    width: 15.8125rem;
    height: 4rem;
    margin-top: 5rem;
  }
}
.info {
  position: relative;
  padding-bottom: 3.75rem;
  margin-top: -8.125rem;
  &__inner {
    position: relative;
    max-width: 73.25em;
    border-radius: 1.5625em;
    background: rgba(51, 51, 51, 0.90);
    backdrop-filter: blur(0.469rem);
    padding: 2.5rem 17.8125rem 2.5rem 2.5rem;
  }
  &__items {
    display: flex;
  }
  &__item {
    position: relative;
    max-width: 15.3125rem;
    min-width: 12.8125rem;
    &:not(:last-child) {
      margin-right: 5.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: -2.8125rem;
        width: 0.0625rem;
        height: 100%;
        background: #FFB800;
      }
    }
    &:first-child {
      max-width: 13.25rem;
    }
    &-text {
      font-size: 1.125rem;
      line-height: 130%;
      color: #ffffff;
      span {
        font-weight: 700;
        color: #FFB800;
      }
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: -32.5em;
    bottom: -4.125em;
    max-width: 49.375rem;
    img {
      width: 100%;
    }
  }
}
.calculation {
  padding: 3.75rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 49.375em);
  }
  &__right {
    width: 38.75rem;
    margin-left: 4.375rem;
  }
  h2 {
    span {
      color: #FFB800;
    }
  }
  &__info {
    border-radius: 1.5625rem;
    border: 0.125rem dashed #000;
    background: #FFB800;
    backdrop-filter: blur(0.46875rem);
    padding: 2.8125rem;
    margin-top: 4.375rem;
  }
  &__text {
    font-size: 1.25rem;
    line-height: 130%;
    color: #000;
    span {
      font-weight: 700;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5625rem;
    margin-top: 2.5rem;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 2.8125rem;
      margin-right: 1.25rem;
    }
    &-text {
      font-size: 1.25rem;
      line-height: 130%;
      color: #000;
      span {
        font-weight: 700;
      }
    }
  }
}
.calculation-form {
  position: relative;
  background: #FFF;
  border-radius: 1.5625rem;
  padding: 3.125em 2.8125rem;
  &__title {
    font-family: 'Oswald';
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #333333;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
  }
  &__top .calculation-form__group:not(:last-child) {
    margin-bottom: 2rem;
  }
  &__left {
    width: 12.5rem;
    margin-right: 3.625rem;
  }
  &__right {
    width: calc(100% - 16.125em);
  }
  &__radio-group {
    min-height: 2.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  &__radio {
    &:not(:last-child) {
      margin-right: 1.25rem;
    }
  }
  &__bottom {
    max-width: 19.5em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    margin-top: 2rem;
  }
  &__btn {
    width: 20.625rem;
    height: 4rem;
  }
  &__img {
    position: absolute;
    right: -16.875rem;
    bottom: -7.5rem;
    width: 30.625rem;
    img {
      width: 100%;
    }
  }
}
.catalog {
  background-image: url("../img/catalog-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 9.375rem 0 15.625rem;
  margin: 3.75rem 0 0.9375rem;
  &__inner {
    max-width: 65rem;
  }
  &__top {
    background: #FFB800;
    border-radius: 1.5625rem 1.5625rem 0 0;
    padding-top: 3.125rem;
  }
  h2 {
    text-align: center;
    padding: 0 1rem;
    span {
      color: #ffffff;
    }
  }
  &__tabs-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
  }
  &__tab {
    min-height: 3.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Oswald';
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 0.0125rem;
    text-transform: uppercase;
    color: #333;
    background: #FFB800;
    border-radius: 1.5625rem 1.5625rem 0 0;
    padding: 0.3125rem 2.25rem;
    cursor: pointer;
    &:first-child {
      border-radius: 0 1.5625rem 0 0;
    }
    &:last-child {
      border-radius: 1.5625rem 0 0 0;
    }
    &:not(:first-child) {
      margin-left: 0.25rem;
    }
  }
  .tab--active {
    background: #ffffff;
  }
  &__tabs-body {
    background: #fff;
    backdrop-filter: blur(0.46875rem);
    border-radius: 0 0 1.5625rem 1.5625rem;
    padding: 3.125rem 2.5rem;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
  }
  &__right {
    text-align: center;
    margin-left: 2.8125rem;
  }
  &__img {
    width: 13.75rem;
    margin-right: 3rem;
    img {
      width: 100%;
    }
  }
  &__list {
    width: calc(100% - 16.75rem);
    p {
      font-size: 1.125rem;
      line-height: 130%;
      color: #333;
      margin: 0;
    }
    p:not(:last-child) {
      margin-bottom: 0.3125rem;
    }
    p span {
      font-weight: 700;
    }
  }
  &__text {
    max-width: 12.5rem;
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    color: #333;
    margin: 0 auto 1.5rem;
  }
  &__btn {
    width: 16.0625rem;
    height: 4rem;
  }
}
.products {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 51.04%, rgba(255, 255, 255, 0.00) 100%);
  padding: 0.9375rem 0 1.875rem;
  h2 {
    max-width: 41.875rem;
    text-align: center;
    margin: 0 auto;
    span {
      color: #FFB800;
    }
  }
  &__slider {
    position: relative;
    margin-top: 3.75rem;
  }
  .slick-prev {
    left: -4rem;
  }
  .slick-next {
    right: -4rem;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2.5rem;
  }
  &__btn {
    width: 15.375em;
    height: 4rem;
  }
}
.product-card {
  text-align: center;
  &:not(:last-child) {
    margin-right: 2rem;
  }
  &__img {
    max-width: 10.3125rem;
    height: 6.125rem;
    display: flex;
    justify-content: center;
    margin: 0 auto;
  }
  &__text {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: #333;
    text-transform: uppercase;
    margin-top: 2rem;
  }
}
.transport {
  position: relative;
  background-image: url("../img/transport-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 5.625rem 0;
  margin: 1.875rem 0;
  h2 {
    max-width: 34.375rem;
    text-align: center;
    margin: 0 auto;
  }
  &__cards {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-top: 3.75rem;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 8.125rem;
  }
  &__btn {
    position: relative;
    z-index: 1;
    width: 18.375rem;
    height: 4rem;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 53.75rem;
  }
}
.transport-card {
  position: relative;
  max-width: 23.625rem;
  text-align: center;
  color: #000;
  background: #FFB800;
  border-radius: 1.5625rem;
  padding: 2.1875rem 1.875rem 13.75rem;
  &:not(:last-child) {
    margin-right: 18.75rem;
  }
  &:nth-child(1) {
    margin-bottom: -4.625rem;
    .transport-card__img {
      position: absolute;
      left: -3.625rem;
      bottom: -7.1875rem;
      width: 30.875rem;
    }
  }
  &:nth-child(2) {
    .transport-card__img {
      position: absolute;
      left: -5.9375rem;
      bottom: -11.875rem;
      width: 35.5rem;
    }
  }
  &__title {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__info {
    margin-top: 2rem;
    p span {
      display: block;
      font-size: 1.25rem;
      line-height: 130%;
    }
    p span:nth-child(1) {
      font-weight: 700;
    }
  }
}
.about {
  padding: 1.875rem 0 3.125rem;
  &__cards {
    max-width: 82rem;
    display: flex;
    justify-content: space-between;
    border-radius: 1.5625rem;
    margin: 0 auto;
  }
}
.about-card {
  max-width: 24.375rem;
  background: #FFB800;
  border-radius: 1.5625rem 0 0 1.5625rem;
  padding: 3rem 3rem 2rem;
  &:nth-child(2) {
    position: relative;
    z-index: 1;
    background: #333;
    transition: all 1.5s;
    .about-card__title {
      color: #ffffff;
    }
    .about-card__text {
      color: #ffffff;
    }
  }
  &:nth-child(3) {
    position: relative;
    z-index: 2;
    transition: all 2s;
  }
  &:not(:first-child) {
    margin-left: -2rem;
  }
  &:last-child {
    border-radius: 1.5625rem;
  }
  &__img {
    max-width: 11.875rem;
    margin: 0 auto;
  }
  &__desc {
    margin-top: 1.25rem;
  }
  &__title {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #000;
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: #000;
    margin-top: 1.25rem;
  }
}
.about-card--big {
  max-width: 36.25rem;
  display: flex;
  flex-direction: column;
  h2 {
    margin-bottom: auto;
  }
  .about-card__wrap {
    display: flex;
    align-items: center;
    margin-top: 6.125rem;
  }
  .about-card__img {
    flex-shrink: 0;
    margin-right: 2rem;
  }
  .about-card__desc {
    margin: 0;
  }
}
.warning {
  background: #333;
  padding: 6.25rem 0 26.25rem;
  &__top {
    display: flex;
    justify-content: space-between;
  }
  h2 {
    max-width: 41.875rem;
    color: #ffffff;
    span {
      color: #FFB800;
    }
  }
  &__subtitle {
    max-width: 38.125rem;
    font-size: 1.5rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 2rem;
  }
  &__info {
    position: relative;
    height: 100%;
    margin-left: 2rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 125em;
      height: 100%;
      background: #FFB800;
      border-radius: 1.5625rem 0 0 1.5625rem;
    }
  }
  &__info-wrap {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    background: #FFB800;
    border-radius: 1.5625rem 0 0 1.5625rem;
    padding: 2rem;
    i {
      flex-shrink: 0;
      width: 4.6875rem;
      margin-right: 1.375rem;
    }
    span {
      max-width: 16.875rem;
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #333;
    }
  }
  &__cards {
    display: flex;
    justify-content: space-between;
    margin-top: 4.5rem;
  }
}
.warning-card {
  max-width: 20.3125rem;
  text-align: center;
  &:not(:first-child) {
    margin-left: 2rem;
  }
  &:nth-child(2) {
    transition: all 1.5s;
  }
  &:nth-child(3) {
    transition: all 2s;
  }
  &__img {
    max-width: 12.75rem;
    height: 11.875rem;
    margin: 0 auto;
  }
  &__desc {
    margin-top: 3.5rem;
  }
  &__title {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: #FFB800;
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 1.5rem;
  }
}
.contacts {
  padding-bottom: 4.6875em;
  margin-top: -20.625rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    background: #FFF;
    border-radius: 1.5625rem;
  }
  .map {
    width: 41.25rem;
    border-radius: 1.5625rem 0 0 1.5625rem;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 1.5625rem 0 0 1.5625rem;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 1.5625rem 0 0 1.5625rem;
    }
  }
  &__desc {
    width: calc(100% - 41.25rem);
    padding: 3.125rem;
  }
  h2 {
    text-align: center;
    span {
      color: #FFB800;
    }
  }
  &__images {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
  }
  &__img {
    width: 50%;
    &:not(:first-child) {
      margin-left: 1.125rem;
    }
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 2rem;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.3125rem;
    margin-right: 0.875rem;
    margin-bottom: 0.875rem;
  }
  &__item {
    display: flex;
    align-items: center;
    color: #333;
    i {
      flex-shrink: 0;
      width: 1.25rem;
      margin-right: 0.625rem;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        fill: currentColor;
      }
    }
    span {
      font-size: 1rem;
      line-height: 130%;
    }
    &:hover {
      color: #FFB800;
    }
  }
  &__btn {
    width: 18.0625rem;
    height: 4rem;
    margin-bottom: 0.875rem;
  }
}
.documentation {
  padding-top: 4.625rem;
  h2 {
    padding: 0 6.25rem;
  }
  &__slider {
    position: relative;
    margin-top: 3rem;
  }
  &__slider-wrapper {
    padding: 0 6.25rem;
  }
}
.documentation-card {
  filter: blur(0.125rem);
  &:not(:last-child) {
    margin-right: 0.875rem;
  }
  img {
    width: 100%;
  }
}
.delivery {
  position: relative;
  background-image: url("../img/delivery-bg.webp");
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  padding: 18.125rem 0 4.6875rem;
  margin-top: -9.375rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding: 0 6.25rem;
  }
  &__left {
    width: 27.5rem;
    margin-right: 2.5rem;
  }
  &__right {
    width: calc(100% - 30rem);
  }
  ul {
    margin-top: 2.8125rem;
    li {
      font-size: 1.25rem;
      line-height: 130%;
      color: #333;
      list-style: disc;
      list-style-position: inside;
    }
    li:not(:first-child) {
      margin-top: 1.25rem;
    }
  }
  &__img {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 17rem;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 18.375rem;
    img {
      width: 100%;
    }
  }
}
.delivery-form {
  max-width: 23.75rem;
  background: #FFB800;
  border-radius: 1.5625rem;
  padding: 3.125rem 2.8125rem;
  margin-left: auto;
  &__title {
    font-family: 'Oswald';
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #333333;
  }
  &__subtitle {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    color: #333333;
    margin-top: 1.5rem;
    span {
      font-weight: 400;
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    gap: 1.5rem;
    margin-top: 1.5rem;
  }
  &__group--100 {
    grid-column: 1/3;
  }
  &__btn {
    grid-column: 1/3;
    width: 13.75rem;
    height: 3.125rem;
  }
  &__check {
    grid-column: 1/3;
  }
}

@media screen and (max-width: 1850px) {
  .delivery {
    &__img2 {
      width: 9.375rem;
    }
  }
}

@media screen and (max-width: 1600px) {
  .info {
    &__img {
      right: -12.5rem;
      max-width: 31.25rem;
    }
  }
  .calculation {
    &__left {
      width: calc(100% - 36.375em);
    }
    &__right {
      width: 34.375rem;
      margin-left: 2.5rem;
    }
    &__info {
      padding: 2.5rem 1.5rem;
    }
  }
  .calculation-form {
    padding: 2.5rem 1.5rem;
    &__left {
      margin-right: 1.5rem;
    }
    &__right {
      width: calc(100% - 14em);
    }
    &__img {
      right: -14rem;
      width: 25rem;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    &__subtitle {
      margin-top: 2rem;
    }
    &__btn {
      margin-top: 3.75rem;
    }
  }
  .info {
    &__inner {
      padding-right: 15.625rem;
    }
    &__item {
      min-width: auto;
      &:not(:last-child) {
        margin-right: 2.5rem;
        &:after {
          right: -1.25rem;
        }
      }
      &-text {
        font-size: 1.125rem;
      }
    }
    &__img {
      right: -15rem;
    }
  }
  .calculation {
    &__item {
      &-text {
        font-size: 1.125rem;
      }
    }
  }
  .catalog {
    &__tab {
      font-size: 1.125rem;
    }
  }
  .product-card {
    &:not(:last-child) {
      margin-right: 1rem;
    }
    &__img {
      max-width: 7.5em;
      height: 5rem;
    }
    &__text {
      font-size: 1.125rem;
      margin-top: 1.25rem;
    }
  }
  .about {
    &__cards {
      display: grid;
      grid-template-columns: repeat(2, calc(50%));
      background: #FFB800;
      border-radius: 1.5625rem;
    }
  }
  .about-card {
    max-width: 100%;
    background: transparent;
    border-radius: 0;
    &:not(:first-child) {
      margin-left: 0;
    }
    &__title {
      font-size: 1.375rem;
    }
    &__img {
      margin: 0;
    }
  }
  .about-card--big {
    grid-column: 1/3;
  }
  .contacts {
    .map {
      width: 31.25rem;
    }
    &__desc {
      width: calc(100% - 31.25rem);
    }
  }
  .documentation {
    h2 {
      padding: 0;
    }
    &__slider-wrapper {
      padding: 0;
    }
    .slick-prev {
      left: -4rem;
    }
    .slick-next {
      right: -4rem;
    }
  }
  .delivery {
    &__inner {
      padding: 0;
    }
    &__img {
      width: 9.375rem;
    }
  }
}

@media screen and (max-width: 1181px) {
  .products {
    &__slider-wrapper {
      padding: 0 3.75rem;
    }
    .slick-prev {
      left: 0;
    }
    .slick-next {
      right: 0;
    }
  }
  .transport-card {
    &:not(:last-child) {
      margin-right: 9.375rem;
    }
  }
  .documentation {
    h2 {
