body {
  font-family: 'NotoSans', sans-serif;
  background-image: url("../img/body-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  overflow: hidden;
}
@media (min-width: 1400px) {
  .container-fluid {
    max-width: 1700px;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Oswald';
  font-weight: 700;
  color: #333;
}
h1 {
  font-family: 'Oswald';
  font-size: 4rem;
  line-height: 130%;
  font-weight: 700;
  letter-spacing: 0.04rem;
  text-transform: uppercase;
}
h2 {
  font-size: 2.25rem;
  line-height: 130%;
  letter-spacing: 0.0225rem;
  text-transform: uppercase;
  margin: 0;
}
button {
  cursor: pointer;
}
.btn-black {
  border-radius: 3.125rem;
  border: 0.125rem solid #FFB800;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.54) 0%, #000 100%);
  box-shadow: 0 0.25rem 0.9375rem 0 rgba(0, 0, 0, 0.50);
  span {
    font-size: 1.25rem;
    line-height: 120%;
    text-transform: uppercase;
    color: #FFB800;
  }
  &:hover {
    background: linear-gradient(180deg, #939393 0%, #939393 100%);
    span {
      color: #333;
    }
  }
  &:active,
  &:focus {
    background: #333;
    span {
      color: #FFB800;
    }
  }
}
.form {
  color: #333;
  &-group {
    padding: 0;
    margin: 0;
    border: none;
    .input-title {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      margin-bottom: 0.625rem;
    }
    select {
      position: relative;
      height: 2.5rem;
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      font-size: 1rem;
      line-height: 130%;
      color: #939393;
      border-radius: 0.3125rem;
      border: 0.0625rem solid #939393;
      background-color: #ffffff;
      background-image: url("../img/select-arrow.svg");
      background-repeat: no-repeat;
      background-position: top 50% right 0.9375rem;
      background-size: 0.9375rem 0.625rem;
      padding: 0 2rem 0 0.9375rem;
      &:focus,
      &:active {
        color: #333;
      }
    }
    input {
      width: 100%;
      height: 2.5rem;
      font-size: 1rem;
      line-height: 130%;
      color: #333333;
      background: #fff;
      border-radius: 0.3125rem;
      border: 0.0625rem solid #939393;
      padding: 0 0.9375rem;
      &::placeholder {
        color: #939393;
      }
    }
  }
  &-radio {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      font-size: 1rem;
      line-height: 130%;
      color: #333333;
      padding-left: 1.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.25rem;
        height: 1.25rem;
        background: transparent;
        border: 0.0625rem solid #939393;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0.3125rem;
        left: 0.3125rem;
        width: 0.625rem;
        height: 0.625rem;
        background: #333;
        border-radius: 50%;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      font-size: 0.875rem;
      line-height: 130%;
      padding-left: 1.875rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.125rem;
        left: 0;
        width: 1.25rem;
        height: 1.25rem;
        background: transparent;
        border: 0.0625rem solid #333;
        border-radius: 0.125rem;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0.375rem;
        left: 0.25rem;
        width: 0.75rem;
        height: 0.5625rem;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
}
.tabs {
  &__content {
    display: none;
  }
  &__content--active {
    display: flex;
  }
}
.slick-prev {
  left: 0;
  width: 3.75rem;
  height: 3.75rem;
  font-size: 0;
  border: 0.125rem solid #FFB800;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.54) 0%, #000 100%);
  box-shadow: 0 0.25rem 0.9375rem 0 rgba(0, 0, 0, 0.50);
  border-radius: 50%;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1.875rem;
    height: 1.5625rem;
    background-image: url("../img/prev.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    background: linear-gradient(180deg, #939393 0%, #939393 100%);
    span {
      color: #333;
    }
  }
  &:active,
  &:focus {
    background: #333;
    span {
      color: #FFB800;
    }
  }
}
.slick-next {
  right: 0;
  width: 3.75rem;
  height: 3.75rem;
  font-size: 0;
  border: 0.125rem solid #FFB800;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.54) 0%, #000 100%);
  box-shadow: 0 0.25rem 0.9375rem 0 rgba(0, 0, 0, 0.50);
  border-radius: 50%;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1.875rem;
    height: 1.5625rem;
    background-image: url("../img/next.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    background: linear-gradient(180deg, #939393 0%, #939393 100%);
    span {
      color: #333;
    }
  }
  &:active,
  &:focus {
    background: #333;
    span {
      color: #FFB800;
    }
  }
}
.slick-dots {
  left: 50%;
  transform: translate(-50%, 0);
  bottom: -1.5rem;
  display: none !important;
  li {
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
    button {
      box-shadow: unset;
    }
  }
  .slick-active {
    background: #FFB800;
  }
}
.text-animation {
  transform: translate(0px, 120%);
  opacity: 0;
  transition: all 0.8s;
}
.text-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.btn-animation {
  transform: translate(0px, 120%);
  opacity: 0;
  transition: all 0.8s;
}
.btn-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-animation {
  transform: translate(120%, 0);
  opacity: 0;
  transition: all 1s;
}
.right-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.left-animation {
  transform: translate(-120%, 0);
  opacity: 0;
  transition: all 1s;
}
.left-animation.active {
  transform: translate(0,0);
  opacity: 1;
}

@media screen and (max-width: 1400px) {
  .btn-black {
    span {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 1181px) {
  .slick-prev {
    width: 3.125rem;
    height: 3.125rem;
    &:after {
      width: 1.375rem;
      height: 1rem;
    }
  }
  .slick-next {
    width: 3.125rem;
    height: 3.125rem;
    &:after {
      width: 1.375rem;
      height: 1rem;
    }
  }
}

@media screen and (max-width: 992px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2rem;
  }
  .slick-dots {
    display: flex !important;
  }
}

@media screen and (max-width: 576px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.75rem;
  }
}